.menu {
  position: static;
  max-width: 620px;
  overflow: auto;


  @include vp-767 {
    position: absolute;
    max-width: 250px;
    overflow: hidden;
  }




  &__list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    gap: 10px 33px;
    transition: all 0.3s ease 0s;
    transform-origin: right top;

    @include vp-1023 {
      gap: 10px 23px;
    }

    @include vp-767 {
      flex-direction: column;
      padding-top: 75px;
      transform: translateX(350%);
    }
  }

  &__item {
    a {
      color: inherit;
      padding: 5px;
      transition: all 0.5s ease 0s;
      border: none;
      border-radius: 3px;
      font-size: 16px;
      line-height: 19px;
      font-weight: 500;

      @include vp-1023 {
        font-size: 14px;
        line-height: 17px;
      }

      @include vp-767 {
        font-size: 18px;
        line-height: 22px;
      }

      @media (hover: hover) {
        &:hover:not(.focus-visible) {
          text-decoration: underline;
          opacity: 0.7;
        }
      }

      &:focus {
        outline: 2px solid $color-default-white;
      }
    }
  }
}
